const DECOR_CLASS = 'decor';
const DECOR_DARK_CLASS ='decor--dark'

const ELEMENTS_CLASS_ARRAY = ['main-nav', 'advantages', 'catalog', 'brands', 'credit', 'contacts'];

ELEMENTS_CLASS_ARRAY.forEach(className => {
  const element = document.querySelector(`.${className}`);

  const decor = document.createElement('div');

  decor.classList.add(DECOR_CLASS);
  decor.classList.add(`${DECOR_CLASS}--${className}`);

  element.append(decor);
})

export const addDecorDarkClass = () => {
  const decors = document.querySelectorAll(`.${DECOR_CLASS}`);

  decors.forEach(decor => {
    decor.classList.add(DECOR_DARK_CLASS);
  })
};


export const removeDecorDarkClass = () => {
  const decors = document.querySelectorAll(`.${DECOR_CLASS}`);

  decors.forEach(decor => {
    decor.classList.remove(DECOR_DARK_CLASS);
  })
};
